@import "utils";
@import "fonts";
@import "typography";

body {
  background: $c-gray;
  margin: 0;
}

* {
  box-sizing: border-box;
}

.photo {
  position: relative;
}

.sportsman {
  width: 100%;
  border-radius: 200px;
  transition: all 1s linear;
}

.number {
  position: absolute;
  top: 100px;
  right: 10px;
}

.details {
  color: #2f4f4f;
  font-family: Arial, sans-serif;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  margin: 8px 0;
}

.extra {
  font-size: 18px;
  line-height: 21px;
  color: black;
  text-align: center;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 30px;
  padding: 30px 55px 0;
}

.person {
  transition: all 1s linear;
}

.sportsman:hover {
  border-radius: 60px;
  position: absolute;
  width: 240px;
  height: 240px;
}

.person:hover {
  height: 100%;
  border-radius: 12px;
  background: #eee;
  text-align: bottom;
}

@media (min-width: 500px) and (max-width: 750px) {

  .container {
    grid-template-columns: repeat(2, minmax(200px, 1fr));
  }
}
